          }
        ],
        yaml: [
          {
            name: 'ÈÖçÁΩÆÊñá‰ª∂',
            description: 'YAMLÈÖçÁΩÆÊñá‰ª∂Ê†ºÂºè',
            preview: 'database:\n  host: localhost\n  port: 3306',
            data: `database:
  host: localhost
  port: 3306
  username: root
  password: password
  dbname: myapp

cache:
  enabled: true
  ttl: 3600
  provider: redis

logging:
  level: info
