  },
  mounted() {
    this.checkMobile()
    window.addEventListener('resize', this.checkMobile)
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.checkMobile)
  },
  methods: {
    checkMobile() {
      this.isMobile = DeviceUtil.isMobile()
    },

    handleViewChange() {
      // ÁßªÂä®Á´ØËßÜÂõæÂàáÊç¢Êó∂ÁöÑÂ§ÑÁêÜ
    },
    handleFormatChange() {
      this.convertData()
    },

    convertData() {
      if (!this.inputData.trim()) {
        this.outputData = ''
        this.validationResult = { isValid: null, message: '' }
        return
      }

      try {
        // È™åËØÅËæìÂÖ•Ê†ºÂºè
        const parsedData = this.parseInput(this.inputData, this.inputFormat)
        this.validationResult = { isValid: true, message: 'ËæìÂÖ•Ê†ºÂºèÈ™åËØÅÈÄöËøá' }
        
        // ËΩ¨Êç¢‰∏∫ËæìÂá∫Ê†ºÂºè
        this.outputData = this.formatOutput(parsedData, this.outputFormat)
      } catch (error) {
        this.validationResult = { isValid: false, message: 'Ê†ºÂºèÈîôËØØ: ' + error.message }
        this.outputData = ''
      }
    },

    parseInput(data, format) {
      switch (format) {
        case 'json':
          return JSON.parse(data)
        case 'xml':
          return this.parseXML(data)
        case 'yaml':
          return this.parseYAML(data)
        case 'toml':
          return this.parseTOML(data)

        default:
          throw new Error('‰∏çÊîØÊåÅÁöÑËæìÂÖ•Ê†ºÂºè')
      }
    },

    formatOutput(data, format) {
      switch (format) {
        case 'json':
          return JSON.stringify(data, null, 2)
        case 'xml':
          return this.toXML(data)
        case 'yaml':
          return this.toYAML(data)
        case 'toml':
          return this.toTOML(data)

        default:
          throw new Error('‰∏çÊîØÊåÅÁöÑËæìÂá∫Ê†ºÂºè')
      }
    },

    parseXML(xmlString) {
      // ÁÆÄÂåñÁöÑXMLËß£ÊûêÔºàÂÆûÈôÖÈ°πÁõÆ‰∏≠Â∫î‰ΩøÁî®‰∏ì‰∏öÁöÑXMLËß£ÊûêÂ∫ìÔºâ
      const parser = new DOMParser()
      const xmlDoc = parser.parseFromString(xmlString, 'text/xml')
      
      if (xmlDoc.getElementsByTagName('parsererror').length > 0) {
        throw new Error('XMLÊ†ºÂºèÈîôËØØ')
      }
      
      return this.xmlToObject(xmlDoc.documentElement)
    },

    xmlToObject(node) {
      const obj = {}
      
      // Â§ÑÁêÜÂ±ûÊÄß
      if (node.attributes && node.attributes.length > 0) {
        obj['@attributes'] = {}
        for (let i = 0; i < node.attributes.length; i++) {
          const attr = node.attributes[i]
          obj['@attributes'][attr.name] = attr.value
        }
      }
      
      // Â§ÑÁêÜÂ≠êËäÇÁÇπ
      if (node.childNodes && node.childNodes.length > 0) {
        for (let i = 0; i < node.childNodes.length; i++) {
          const child = node.childNodes[i]
          
          if (child.nodeType === 3) { // ÊñáÊú¨ËäÇÁÇπ
            const text = child.textContent.trim()
            if (text) {
              obj['#text'] = text
            }
          } else if (child.nodeType === 1) { // ÂÖÉÁ¥†ËäÇÁÇπ
            const childName = child.nodeName
            const childObj = this.xmlToObject(child)
            
            if (obj[childName]) {
              if (!Array.isArray(obj[childName])) {
                obj[childName] = [obj[childName]]
              }
              obj[childName].push(childObj)
            } else {
              obj[childName] = childObj
            }
          }
        }
      }
      
      return obj
    },

    toXML(obj, rootName = 'root') {
      let xml = '<?xml version="1.0" encoding="UTF-8"?>\n'
      xml += this.objectToXML(obj, rootName)
      return xml
    },

    objectToXML(obj, nodeName) {
      if (typeof obj !== 'object' || obj === null) {
        return `<${nodeName}>${this.escapeXML(String(obj))}</${nodeName}>`
      }
      
      if (Array.isArray(obj)) {
        return obj.map(item => this.objectToXML(item, nodeName)).join('\n')
      }
      
      let xml = `<${nodeName}`
      let content = ''
      
      // Â§ÑÁêÜÂ±ûÊÄß
      if (obj['@attributes']) {
        for (const [key, value] of Object.entries(obj['@attributes'])) {
          xml += ` ${key}="${this.escapeXML(String(value))}"`
        }
      }
      
      xml += '>'
      
      // Â§ÑÁêÜÊñáÊú¨ÂÜÖÂÆπ
      if (obj['#text']) {
        content += this.escapeXML(String(obj['#text']))
      }
      
      // Â§ÑÁêÜÂ≠êÂÖÉÁ¥†
      for (const [key, value] of Object.entries(obj)) {
        if (key !== '@attributes' && key !== '#text') {
          if (Array.isArray(value)) {
            content += value.map(item => this.objectToXML(item, key)).join('')
          } else {
            content += this.objectToXML(value, key)
          }
        }
      }
      
      xml += content + `</${nodeName}>`
      return xml
    },

    escapeXML(str) {
      return str.replace(/[<>&'"]/g, (char) => {
        switch (char) {
          case '<': return '&lt;'
          case '>': return '&gt;'
          case '&': return '&amp;'
          case "'": return '&apos;'
          case '"': return '&quot;'
          default: return char
        }
      })
    },

    parseYAML(yamlString) {
      // ÁÆÄÂåñÁöÑYAMLËß£ÊûêÔºàÂÆûÈôÖÈ°πÁõÆ‰∏≠Â∫î‰ΩøÁî®js-yamlÂ∫ìÔºâ
      const lines = yamlString.split('\n')
      const result = {}

      for (const line of lines) {
        if (line.trim() === '' || line.trim().startsWith('#')) continue

        const content = line.trim()

        if (content.includes(':')) {
          const [key, value] = content.split(':').map(s => s.trim())
          // ÁÆÄÂåñÂ§ÑÁêÜÔºåÂÆûÈôÖYAMLËß£ÊûêÊõ¥Â§çÊùÇ
          result[key] = value || {}
        }
      }

      return result
    },

    toYAML(obj, indent = 0) {
      let yaml = ''
      const spaces = '  '.repeat(indent)
      
      for (const [key, value] of Object.entries(obj)) {
        if (typeof value === 'object' && value !== null && !Array.isArray(value)) {
          yaml += `${spaces}${key}:\n`
          yaml += this.toYAML(value, indent + 1)
        } else if (Array.isArray(value)) {
          yaml += `${spaces}${key}:\n`
          for (const item of value) {
            yaml += `${spaces}  - ${item}\n`
          }
        } else {
          yaml += `${spaces}${key}: ${value}\n`
        }
      }
      
      return yaml
    },

    parseTOML(tomlString) {
      // ÁÆÄÂåñÁöÑTOMLËß£ÊûêÔºàÂÆûÈôÖÈ°πÁõÆ‰∏≠Â∫î‰ΩøÁî®‰∏ì‰∏öÁöÑTOMLËß£ÊûêÂ∫ìÔºâ
      const lines = tomlString.split('\n')
      const result = {}
      let currentSection = result
      let currentSectionName = ''

      for (const line of lines) {
        const trimmedLine = line.trim()

        // Ë∑≥ËøáÁ©∫Ë°åÂíåÊ≥®Èáä
        if (!trimmedLine || trimmedLine.startsWith('#')) continue

        // Â§ÑÁêÜËäÇÔºàsectionÔºâ
        if (trimmedLine.startsWith('[') && trimmedLine.endsWith(']')) {
          currentSectionName = trimmedLine.slice(1, -1)
          currentSection = result[currentSectionName] = {}
          continue
        }

        // Â§ÑÁêÜÈîÆÂÄºÂØπ
        if (trimmedLine.includes('=')) {
          const [key, ...valueParts] = trimmedLine.split('=')
          const value = valueParts.join('=').trim()

          // ÁÆÄÂçïÁöÑÂÄºÁ±ªÂûãÂ§ÑÁêÜ
          let parsedValue = value
          if (value.startsWith('"') && value.endsWith('"')) {
            parsedValue = value.slice(1, -1) // Â≠óÁ¨¶‰∏≤
          } else if (value === 'true' || value === 'false') {
            parsedValue = value === 'true' // Â∏ÉÂ∞îÂÄº
          } else if (!isNaN(value)) {
            parsedValue = Number(value) // Êï∞Â≠ó
          }

          currentSection[key.trim()] = parsedValue
        }
      }

      return result
    },

    toTOML(obj) {
      // ÁÆÄÂåñÁöÑTOMLÁîüÊàê
      let toml = ''

      // Â§ÑÁêÜÊ†πÁ∫ßÂà´ÁöÑÈîÆÂÄºÂØπ
      for (const [key, value] of Object.entries(obj)) {
        if (typeof value === 'object' && value !== null && !Array.isArray(value)) {
          // ËøôÊòØ‰∏Ä‰∏™ËäÇ
          toml += `\n[${key}]\n`
          for (const [subKey, subValue] of Object.entries(value)) {
            toml += `${subKey} = ${this.formatTOMLValue(subValue)}\n`
          }
        } else {
          // Ê†πÁ∫ßÂà´ÁöÑÈîÆÂÄºÂØπ
          toml += `${key} = ${this.formatTOMLValue(value)}\n`
        }
      }

      return toml.trim()
    },

    formatTOMLValue(value) {
      if (typeof value === 'string') {
        return `"${value}"`
      } else if (typeof value === 'boolean') {
        return value.toString()
      } else if (typeof value === 'number') {
        return value.toString()
      } else if (Array.isArray(value)) {
        return `[${value.map(v => this.formatTOMLValue(v)).join(', ')}]`
      } else {
        return `"${String(value)}"`
      }
    },



    formatInput() {
      if (!this.inputData.trim()) return
      
      try {
        const parsed = this.parseInput(this.inputData, this.inputFormat)
        this.inputData = this.formatOutput(parsed, this.inputFormat)
      } catch (error) {
        this.$message.error('Ê†ºÂºèÂåñÂ§±Ë¥•: ' + error.message)
      }
    },

    getPlaceholder(format) {
      const placeholders = {
        json: 'ËØ∑ËæìÂÖ•JSONÊï∞ÊçÆÔºåÂ¶ÇÔºö{"name": "Âº†‰∏â", "age": 25}',
        xml: 'ËØ∑ËæìÂÖ•XMLÊï∞ÊçÆÔºåÂ¶ÇÔºö<user><name>Âº†‰∏â</name></user>',
        yaml: 'ËØ∑ËæìÂÖ•YAMLÊï∞ÊçÆÔºåÂ¶ÇÔºöname: Âº†‰∏â\nage: 25',
        toml: 'ËØ∑ËæìÂÖ•TOMLÊï∞ÊçÆÔºåÂ¶ÇÔºöname = "Âº†‰∏â"\nage = 25'
      }
      return placeholders[format] || 'ËØ∑ËæìÂÖ•Êï∞ÊçÆ'
    },

    getFormatName(format) {
      const names = {
        json: 'JSON',
        xml: 'XML',
        yaml: 'YAML',
        toml: 'TOML'
      }
      return names[format] || format.toUpperCase()
    },

    useTemplate(template) {
      this.inputFormat = this.activeTemplateTab
      this.inputData = template.data
      this.convertData()
    },

    loadSample() {
      const samples = {
