  },
  "hobbies": ["ËØª‰π¶", "Ê∏∏Ê≥≥", "ÁºñÁ®ã"]
}`,
        xml: `<?xml version="1.0" encoding="UTF-8"?>
<user>
  <name>Âº†‰∏â</name>
  <age>25</age>
  <email>zhangsan@example.com</email>
  <address>
    <city>Âåó‰∫¨</city>
