      this.outputData = ''
      this.validationResult = { isValid: null, message: '' }
    },

    async copyResult() {
      if (!this.outputData) {
        this.$message.warning('Ê≤°ÊúâÂèØÂ§çÂà∂ÁöÑÂÜÖÂÆπ')
        return
      }

      try {
